import QSTabs from '../../../components/QS-tabs/QS-tabs.vue';
import uniGrid from '../../../components/uni-grid/uni-grid.vue';
import uniGridItem from '../../../components/uni-grid-item/uni-grid-item.vue';


import {
	mapState
} from 'vuex'

let _this = this;
let loginResult;

export default {
	
	
	components: {
		QSTabs,
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			title: 'Hello',
			br:'\n',
			tabs: ['作品','相册','文章'],
			current: 0,
			tabsHeight: 0,
			dx: 0,
			nickname:'昵称',
			face:'',
			uid:'',
			// isLogin1:false,
			
			works:{},
			images:[]
			
			
		}
	},
	
	onLoad() {
		loginResult = this.checkLogin('/pages/tabbar-5/tabbar-5', 0);
		if(!loginResult){return;}
	},
	
	onShow() {
		let userinfo = uni.getStorageSync('userinfo');
		console.log('userinfo:');
		console.log(userinfo);
		
		this.nickname = userinfo.nickname;
		this.face = userinfo.headPortraitImg;
		this.uid = userinfo.id;
		
		this.getWorksByUserId();
		this.getImagesByUserId();
		
	},
	
	onPullDownRefresh:function(){
		this.getWorksByUserId();
		setTimeout(function(){
			uni.stopPullDownRefresh();
		}, 1000);
	},
	
	methods: {
		
		_gotoPage:function(pageUrl){
			this.gotoPage(pageUrl);
		},
		change(index) {
			this.current = index;
		},
		swiperChange({detail: { current }}) {
			this.current = current;
		},
		transition({detail: { dx }}) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({detail: { current }}) {
			this.$refs.tabs.setFinishCurrent(current);
		},
		isLogin(){
			var SNAME = uni.getStorageSync('SNAME');
			if(SNAME != ''){
				return true;
			}else{
				return false;
			}
		},
		
		//获取作品：通过用户ID
		getWorksByUserId:function(){
			uni.request({
				url:this.createApiUrl('/works/get/uid'),
				data:{
					uid:this.uid
				},
				success: (res) => {
					this.works = res.data.data;
					console.log(this.works);
				}
			})
		},
		
		//获取相册图片
		getImagesByUserId:function(){
			uni.request({
				url:this.createApiUrl('user/images'),
				data:{
					uid:this.uid
				},
				success: (res) => {
					console.log(res);
					this.images = res.data.data;
					console.log(this.images);
					
				}
			})
		},
		
		//删除作品
		deleteWorks:function(worksId){
			console.log(worksId);
			uni.request({
				url: this.createApiUrl('works/delete'),
				method: 'POST',
				header: {
				        'content-type': 'application/x-www-form-urlencoded'  
				},
				data: {
					worksId: worksId
				},
				success: res => {
					console.log(res);
					if(res.data.code == 200){
						uni.showToast({
							title:'删除成功'
						})
					}else{
						uni.showToast({
							title:'删除失败'
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//预览图片
		previewImg:function(index, urls){
			uni.previewImage({
				current:index,
				urls:urls,
				indicator:"default",
				success: (res) => {
					console.log(res);
				},
				fail: (err) => {
					console.log(err);
				}
			})
		},
		
		confirmDelete:function(worksId){
			uni.showModal({
				title: '删除作品',
				content: '您是否要删除作品？',
				showCancel: true,
				cancelText: '算了',
				confirmText: '是的',
				success: res => {
					if(res.confirm){
						this.deleteWorks(worksId);
					}else if(res.cancel){
						console.log('用户点击取消');
					}
					
				},
				fail: () => {},
				complete: () => {
					uni.startPullDownRefresh({
						success() {
							setTimeout(function(){
								uni.stopPullDownRefresh();
							}, 1000);
						}
					})
				}
			});
		}
		

	}
}
